
const initialState = {
  payload: {},
  loading: false,
  successful: false,
}

export const passwordRecoveryReducer =  (state = initialState, action) => {
  switch (action.type){

    case "PASSWORD_RECOVERY_PENDING": {
      return {
        ...state,
        loading: true,
      }
    }

    case "PASSWORD_RECOVERY_FULFILLED": {
      return {
        ...state,
        loading: false,
        successful : true,
        payload: action.payload.data,
      }
    }

    case "PASSWORD_RECOVERY_REJECTED": {
      return {
        ...state,
        loading: false,
        successful: false,
        payload: action.payload.data,
      }
    }

    default: {
      return {
        ...state
      }
    }

  }

}