
const initialState = {
  search_payload: {},
  loading: false,
  successful: false,
}

export const searchCarsReducer =  (state = initialState, action) => {
  switch (action.type){

    case "SEARCH_CARS_PENDING": {
      return {
        ...state,
        loading: true,
      }
    }

    case "SEARCH_CARS_FULFILLED": {
      return {
        ...state,
        loading: false,
        successful : true,
        search_payload: action.payload.data,
      }
    }

    case "SELL_CAR_REJECTED": {
      return {
        ...state,
        loading: false,
        successful: false,
        search_payload: action.payload.data,
      }
    }

    default: {
      return {
        ...state
      }
    }

  }

}