const initialState = {
  cars_payload: {},
  get_cars_loading: true,
  get_cars_successful: false,
}

export const viewVendorCarsReducer =  (state = initialState, action) => {
  switch (action.type){

    case "VIEW_VENDOR_CARS_PENDING": {
      return {
        ...state,
        get_cars_loading: true,
      }
    }

    case "VIEW_VENDOR_CARS_FULFILLED": {
      return {
        ...state,
        get_cars_loading: false,
        get_cars_successful: true,
        cars_payload: action.payload.data,
      }
    }

    case "VIEW_VENDOR_CARS_REJECTED": {
      return {
        ...state,
        get_cars_loading: false,
        get_cars_successful: false,
        cars_payload: action.payload.data,
      }
    }

    default: {
      return {
        ...state
      }
    }

  }
}