let initState = {
    "login_payload": {},
    "login_user_loading": false,
    "login_user_successful": false,
    "signup_payload": {},
    "signup_user_loading": false,
    "signup_user_successful": false
}

export const onboardingReducer = (state = initState, action) =>{
    switch(action.type) {
        case `LOGIN_USER_PENDING` : {
            return {
                ...state,
                "login_user_loading": true
            }
        }
        case `LOGIN_USER_FULFILLED` : {
            return {
                ...state,
                "login_user_loading": false,
                "login_user_successful": true,
                "login_payload": action.payload.data
            }
        }
        case `LOGIN_USER_REJECTED` : {
            return {
                ...state,
                "login_user_loading": false,
                "login_user_successful": false,
                "login_payload": action.payload
            }
        }
        case `SIGNUP_USER_PENDING` : {
            return {
                ...state,
                "signup_user_loading": true
            }
        }
        case `SIGNUP_USER_FULFILLED` : {
            return {
                ...state,
                "signup_user_loading": false,
                "signup_user_successful": true,
                "signup_payload": action.payload.data
            }
        }
        case `SIGNUP_USER_REJECTED` : {
            return {
                ...state,
                "signup_user_loading": false,
                "signup_user_successful": false,
                "signup_payload": action.payload
            }
        }
        case `LOGOUT_USER` : {
            return {
                ...state,
                "login_user_successful": false,
                "login_payload": {}
            }
        }
        default: {
            return {
                ...state
            }
        }
    }
}
