import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import * as Pages from './loadpages';

class App extends Component {
    render() {
        let view;
        if (localStorage.getItem("ax_auth")) {
            view = (
                <Switch>
                    <Route exact path="/" component={Pages.Home}/>
                    <Route path="/cars" component={Pages.CarsList}/>
                    <Route path="/logout" component={Pages.Logout} />
                    {/* <Roiute component={errorpage} /> */}
                </Switch>
            )
        }else {
            view = (
                <Switch>
                    <Route exact path="/" component={Pages.Home}/>                   
                    <Route exact path="/cars" component={Pages.CarsList}/>
                    <Route path="/cars/:id" component={Pages.CarDetail}/>
                    <Route path="/sell" component={Pages.SellCar}/>
                    <Route path="/dealer" component={Pages.searchDealer}/>
                    <Route path="/login" component={Pages.Login}/>
                    <Route path="/passwordRecovery" component={Pages.PasswordRecovery}/>
                    <Route path="/signup" component={Pages.Signup}/>
                    {/* <Roiute component={errorpage} /> */}
                </Switch> 
            )
        }
        return (
            <Router>
                {view}
            </Router>
        );
    }
}

export default App;
