import {combineReducers} from 'redux';
import { onboardingReducer } from './onboarding';
import {viewAllCarsReducer} from './viewAllCars';
import {viewMyCarsReducer} from './viewMyCars';
import {viewVendorCarsReducer} from './viewVendorCars';
import {sellCarReducer} from './sellcar';
import {searchCarsReducer} from './searchCars';
import { passwordRecoveryReducer } from './passwordRecovery';

export default combineReducers({
    Onboarding: onboardingReducer,
    Viewallcars: viewAllCarsReducer,
    Viewmycars: viewMyCarsReducer,
    Viewvendorcars: viewVendorCarsReducer,
    Sellcar: sellCarReducer ,
    Searchcars: searchCarsReducer,
    Passwordrecovery: passwordRecoveryReducer
})