
const initState = {
  cars_payload : {},
  get_cars_loading: false,
  get_cars_succesfull: false,
}

export const viewMyCarsReducer = (state = initState,  action) => {

  switch(action.type) {
    case `VIEW_MY_CARS_PENDING`:
      return {
        ...state,
        get_cars_loading: true
      }
    case `VIEW_MY_CARS_FULFILLED`:
      return {
        ...state,
        get_cars_loading: false,
        cars_payload: action.payload.data
      }
    case `VIEW_MY_CARS_REJECTED`:
      return {
        ...state,
        get_cars_loading: false,
        cars_payload: action.payload.data
      }

    default:
      return {
        ...state
      }
  }
}