
const initialState = {
  sell_payload: {},
  sell_car_loading: false,
  sell_car_successful: false,
}

export const sellCarReducer =  (state = initialState, action) => {
  switch (action.type){

    case "SELL_CAR_PENDING": {
      return {
        ...state,
        sell_car_loading: true,
      }
    }

    case "SELL_CAR_FULFILLED": {
      return {
        ...state,
        sell_car_loading: false,
        sell_car_successful : true,
        sell_payload: action.payload.data,
      }
    }

    case "SELL_CAR_REJECTED": {
      return {
        ...state,
        sell_car_loading: false,
        sell_car_successful: false,
        sell_payload: action.payload.data,
      }
    }

    default: {
      return {
        ...state
      }
    }

  }

}